import React, { useState, useEffect } from 'react';
import { Calendar, Clock, Users, Briefcase, User, CheckCircle, AlertCircle, ArrowUpCircle, ArrowDownCircle, Edit, Trash2 } from 'lucide-react';

const ActionTracker = () => {
  // Sample data
  const initialItems = [
    { 
      id: 1, 
      title: 'Q2 Budget Review', 
      description: 'Review and approve Q2 budget allocations', 
      category: 'executive', 
      priority: 'high',
      status: 'in-progress', 
      dueDate: '2025-05-01', 
      assignee: 'Self'
    },
    { 
      id: 2, 
      title: 'Team Meeting', 
      description: 'Discuss next steps with team', 
      category: 'team', 
      priority: 'high',
      status: 'not-started', 
      dueDate: '2025-04-28', 
      assignee: 'Team Lead'
    },
    { 
      id: 3, 
      title: 'Personal Development', 
      description: 'Update personal development roadmap', 
      category: 'personal', 
      priority: 'low',
      status: 'not-started', 
      dueDate: '2025-05-15', 
      assignee: 'Self'
    }
  ];

  // State for items and filters
  const [items, setItems] = useState(initialItems);
  const [filteredItems, setFilteredItems] = useState(initialItems);
  const [filters, setFilters] = useState({
    category: 'all',
    status: 'all'
  });
  const [showForm, setShowForm] = useState(false);
  const [currentItem, setCurrentItem] = useState(null);
  const [newItem, setNewItem] = useState({
    title: '',
    description: '',
    category: 'team',
    priority: 'medium',
    status: 'not-started',
    dueDate: '',
    assignee: ''
  });

  // Load items from localStorage when the component mounts
  useEffect(() => {
    const savedItems = localStorage.getItem('actionTrackerItems');
    if (savedItems) {
      setItems(JSON.parse(savedItems));
    }
  }, []);

  // Save items to localStorage whenever they change
  useEffect(() => {
    localStorage.setItem('actionTrackerItems', JSON.stringify(items));
  }, [items]);

  // Apply filters when filters or items change
  useEffect(() => {
    let result = [...items];
    
    if (filters.category !== 'all') {
      result = result.filter(item => item.category === filters.category);
    }
    
    if (filters.status !== 'all') {
      result = result.filter(item => item.status === filters.status);
    }
    
    setFilteredItems(result);
  }, [filters, items]);

  // Handle filter changes
  const handleFilterChange = (e) => {
    const { name, value } = e.target;
    setFilters(prev => ({
      ...prev,
      [name]: value
    }));
  };

  // Handle input changes
  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setNewItem(prev => ({
      ...prev,
      [name]: value
    }));
  };

  // Handle form submission
  const handleSubmit = (e) => {
    e.preventDefault();
    
    if (!newItem.title || !newItem.dueDate) {
      alert('Title and due date are required');
      return;
    }
    
    if (currentItem) {
      // Update existing item
      setItems(items.map(item => 
        item.id === currentItem.id ? { ...newItem, id: item.id } : item
      ));
    } else {
      // Add new item
      const id = Math.max(0, ...items.map(item => item.id)) + 1;
      setItems([...items, { ...newItem, id }]);
    }
    
    // Reset form
    setNewItem({
      title: '',
      description: '',
      category: 'team',
      priority: 'medium',
      status: 'not-started',
      dueDate: '',
      assignee: ''
    });
    setCurrentItem(null);
    setShowForm(false);
  };

  // Start editing an item
  const handleEdit = (item) => {
    setCurrentItem(item);
    setNewItem(item);
    setShowForm(true);
  };

  // Delete an item
  const handleDelete = (id) => {
    if (window.confirm('Are you sure you want to delete this item?')) {
      setItems(items.filter(item => item.id !== id));
    }
  };

  // Format date for display
  const formatDate = (dateString) => {
    const options = { year: 'numeric', month: 'short', day: 'numeric' };
    return new Date(dateString).toLocaleDateString(undefined, options);
  };

  return (
    <div className="min-h-screen bg-slate-100 p-4">
      <div className="max-w-6xl mx-auto">
        <header className="mb-8 text-center">
          <h1 className="text-3xl font-bold text-slate-800">Action Tracker</h1>
        </header>

        {/* Add/Edit Button */}
        <div className="mb-4 flex justify-end">
          <button
            onClick={() => {
              setCurrentItem(null);
              setNewItem({
                title: '',
                description: '',
                category: 'team',
                priority: 'medium',
                status: 'not-started',
                dueDate: '',
                assignee: ''
              });
              setShowForm(!showForm);
            }}
            className="bg-blue-600 text-white px-4 py-2 rounded-lg hover:bg-blue-700"
          >
            {showForm ? 'Cancel' : 'Add New Item'}
          </button>
        </div>

        {/* Form for adding/editing items */}
        {showForm && (
          <div className="mb-6 bg-white p-6 rounded-lg shadow-sm border border-slate-200">
            <h2 className="text-xl font-semibold mb-4">
              {currentItem ? 'Edit Item' : 'Add New Item'}
            </h2>
            <form onSubmit={handleSubmit} className="space-y-4">
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div>
                  <label className="block text-sm font-medium text-slate-700 mb-1">Title*</label>
                  <input 
                    type="text" 
                    name="title" 
                    value={newItem.title} 
                    onChange={handleInputChange} 
                    className="w-full border border-slate-300 rounded-lg px-3 py-2"
                    required 
                  />
                </div>
                
                <div>
                  <label className="block text-sm font-medium text-slate-700 mb-1">Due Date*</label>
                  <input 
                    type="date" 
                    name="dueDate" 
                    value={newItem.dueDate} 
                    onChange={handleInputChange} 
                    className="w-full border border-slate-300 rounded-lg px-3 py-2"
                    required 
                  />
                </div>
                
                <div>
                  <label className="block text-sm font-medium text-slate-700 mb-1">Category</label>
                  <select 
                    name="category" 
                    value={newItem.category} 
                    onChange={handleInputChange}
                    className="w-full border border-slate-300 rounded-lg px-3 py-2"
                  >
                    <option value="partner">Partner</option>
                    <option value="executive">Executive</option>
                    <option value="team">Team</option>
                    <option value="personal">Personal</option>
                  </select>
                </div>
                
                <div>
                  <label className="block text-sm font-medium text-slate-700 mb-1">Status</label>
                  <select 
                    name="status" 
                    value={newItem.status} 
                    onChange={handleInputChange}
                    className="w-full border border-slate-300 rounded-lg px-3 py-2"
                  >
                    <option value="not-started">Not Started</option>
                    <option value="in-progress">In Progress</option>
                    <option value="completed">Completed</option>
                  </select>
                </div>
                
                <div>
                  <label className="block text-sm font-medium text-slate-700 mb-1">Assignee</label>
                  <input 
                    type="text" 
                    name="assignee" 
                    value={newItem.assignee} 
                    onChange={handleInputChange} 
                    className="w-full border border-slate-300 rounded-lg px-3 py-2" 
                  />
                </div>
                
                <div>
                  <label className="block text-sm font-medium text-slate-700 mb-1">Priority</label>
                  <select 
                    name="priority" 
                    value={newItem.priority} 
                    onChange={handleInputChange}
                    className="w-full border border-slate-300 rounded-lg px-3 py-2"
                  >
                    <option value="high">High</option>
                    <option value="medium">Medium</option>
                    <option value="low">Low</option>
                  </select>
                </div>
                
                <div className="md:col-span-2">
                  <label className="block text-sm font-medium text-slate-700 mb-1">Description</label>
                  <textarea 
                    name="description" 
                    value={newItem.description} 
                    onChange={handleInputChange} 
                    className="w-full border border-slate-300 rounded-lg px-3 py-2 h-24" 
                  />
                </div>
              </div>
              
              <div className="flex justify-end gap-3 pt-2">
                <button
                  type="button"
                  onClick={() => {
                    setShowForm(false);
                    setCurrentItem(null);
                  }}
                  className="px-4 py-2 border border-slate-300 rounded-lg text-slate-700 hover:bg-slate-50"
                >
                  Cancel
                </button>
                <button
                  type="submit"
                  className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700"
                >
                  {currentItem ? 'Update' : 'Add'} Item
                </button>
              </div>
            </form>
          </div>
        )}

        {/* Filter Controls */}
        <div className="mb-6 bg-white p-4 rounded-lg shadow-sm border border-slate-200">
          <div className="flex flex-wrap gap-3">
            <div className="flex items-center">
              <span className="text-sm font-medium text-slate-700 mr-2">Filter by:</span>
            </div>
            
            <select 
              name="category" 
              value={filters.category} 
              onChange={handleFilterChange}
              className="bg-white border border-slate-200 rounded-lg px-3 py-1.5 text-sm shadow-sm"
            >
              <option value="all">All Categories</option>
              <option value="partner">Partner</option>
              <option value="executive">Executive</option>
              <option value="team">Team</option>
              <option value="personal">Personal</option>
            </select>
            
            <select 
              name="status" 
              value={filters.status} 
              onChange={handleFilterChange}
              className="bg-white border border-slate-200 rounded-lg px-3 py-1.5 text-sm shadow-sm"
            >
              <option value="all">All Statuses</option>
              <option value="not-started">Not Started</option>
              <option value="in-progress">In Progress</option>
              <option value="completed">Completed</option>
            </select>
          </div>
        </div>

        {/* Table Grid Layout */}
        <div className="bg-white rounded-lg shadow-sm border border-slate-200 overflow-hidden">
          {filteredItems.length > 0 ? (
            <div className="overflow-x-auto">
              <table className="min-w-full divide-y divide-slate-200">
                <thead className="bg-slate-50">
                  <tr>
                    <th className="px-6 py-3 text-left text-xs font-medium text-slate-500 uppercase tracking-wider">Title</th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-slate-500 uppercase tracking-wider">Category</th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-slate-500 uppercase tracking-wider">Status</th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-slate-500 uppercase tracking-wider">Priority</th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-slate-500 uppercase tracking-wider">Due Date</th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-slate-500 uppercase tracking-wider">Assignee</th>
                    <th className="px-6 py-3 text-right text-xs font-medium text-slate-500 uppercase tracking-wider">Actions</th>
                  </tr>
                </thead>
                <tbody className="bg-white divide-y divide-slate-200">
                  {filteredItems.map((item) => (
                    <tr key={item.id} className="hover:bg-slate-50">
                      <td className="px-6 py-4 whitespace-nowrap">
                        <div className="text-sm font-medium text-slate-900">{item.title}</div>
                        {item.description && (
                          <div className="text-xs text-slate-500 truncate max-w-xs" title={item.description}>
                            {item.description}
                          </div>
                        )}
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap">
                        <span className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium capitalize 
                          ${item.category === 'team' ? 'team-badge' : 
                            item.category === 'personal' ? 'personal-badge' : 
                            item.category === 'executive' ? 'bg-amber-100 text-amber-800' : 
                            'bg-blue-100 text-blue-800'}`}>
                          {item.category}
                        </span>
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap">
                        <span className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium capitalize
                          ${item.status === 'completed' ? 'bg-green-100 text-green-800' :
                            item.status === 'in-progress' ? 'bg-blue-100 text-blue-800' :
                            'bg-gray-100 text-gray-800'}`}>
                          {item.status.replace('-', ' ')}
                        </span>
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap">
                        <span className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium capitalize
                          ${item.priority === 'high' ? 'bg-red-100 text-red-800' :
                            item.priority === 'medium' ? 'bg-yellow-100 text-yellow-800' :
                            'bg-green-100 text-green-800'}`}>
                          {item.priority}
                        </span>
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap">
                        <div className="text-sm text-slate-900">{item.dueDate}</div>
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap">
                        <div className="text-sm text-slate-900">{item.assignee}</div>
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap text-right text-sm font-medium">
                        <button
                          onClick={() => handleEdit(item)}
                          className="text-blue-600 hover:text-blue-900 mr-3"
                        >
                          <Edit className="h-4 w-4 inline" />
                        </button>
                        <button
                          onClick={() => handleDelete(item.id)}
                          className="text-red-600 hover:text-red-900"
                        >
                          <Trash2 className="h-4 w-4 inline" />
                        </button>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          ) : (
            <div className="p-8 text-center">
              <p className="text-slate-500">No items found matching your filters.</p>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default ActionTracker;